.Card {
  display: block;
  position: relative;
  height: 100vh;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  -webkit-transition: opacity 1s ease-in-out;

  -o-transition: opacity 1s ease-in-out;

  transition: opacity 1s ease-in-out;
}

.Card__container {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 220px;
  background: rgba(2,2,2,0.65);

  text-align: left;
  color: white;
}

.Card__content {
  padding: 10px 30px 0;
}
.Card__content--title {
  font-family: 'Lato';
  font-size: 36px;
  font-weight: bold;
}
.Card__content--description {
  font-family: 'Merriweather';
  font-size: 24px;
  line-height: 1.3;
}



